button{
    width: 187px;
height: 56px;
flex-shrink: 0;
border-radius: 50px;
border: 2px solid #FFD540;
background-color: transparent;

color: #FFD540;
font-family: "Montserrat";
font-size: 14px;
font-style: normal;
font-weight: 600;
line-height: normal;
text-transform: uppercase;

display: none; // temporary
}

@media only screen and (min-width:425px) {
    button{
        display: block;
    }
}